import React from "react";
import { useHistory } from "react-router";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { PayPalButton } from "react-paypal-button-v2";
import { faTimesCircle } from "@fortawesome/free-solid-svg-icons";

const CheckoutPage = ({
  setDisplay,
  artInCart,
  winesInCart,
  checkoutPrice,
  shippingCost,
}) => {
  const history = useHistory();
  return (
    <div className="vh-100 vw-100 fixed-top bg-light">
      <div className="container">
        <div className="d-flex justify-content-end">
          <div className="my-2">
            <button
              onClick={() => {
                setDisplay(false);
              }}
              type="button"
              className="btn btn-outline-danger rounded-circle"
            >
              <FontAwesomeIcon icon={faTimesCircle} />
            </button>
          </div>
        </div>
        <div className="row">
          <div className="col-sm-12 col-md-8">
            {artInCart.length > 0 && (
              <table className="table table-hover table-sm">
                <caption>Art</caption>
                <thead>
                  <tr>
                    <th scope="col">#</th>
                    <th scope="col">Title</th>
                    <th scope="col">Artist</th>
                    <th scope="col">Price</th>
                  </tr>
                </thead>
                <tbody>
                  {artInCart.map((art, index) => (
                    <tr key={index}>
                      <th scope="row">{index + 1}</th>
                      <td>{art.name}</td>
                      <td>{art.artist}</td>
                      <td>
                        {Intl.NumberFormat("de-De", {
                          style: "currency",
                          currency: "EUR",
                        }).format(art.unit_amount.value)}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            )}
            <hr/>
            {winesInCart.length > 0 && (
              <table className="table table-hover table-sm">
                <caption>Wine(s)</caption>
                <thead>
                  <tr>
                    <th scope="col">#</th>
                    <th scope="col">Name</th>
                    <th scope="col">Qty</th>
                    <th scope="col">Price</th>
                  </tr>
                </thead>
                <tbody>
                  {winesInCart.map((wine, index) => (
                    <tr key={index}>
                      <th scope="row">{index + 1}</th>
                      <td>{wine.name}</td>
                      <td>{wine.quantity}</td>
                      <td>
                        {Intl.NumberFormat("de-De", {
                          style: "currency",
                          currency: "EUR",
                        }).format(wine.unit_amount.value)}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            )}
          </div>

          <div className="col-sm-12 col-md-4">
            <div className="border border-primary rounded-pill text-center mb-2">
              <h5 className="my-2">
                Total:{" "}
                {Intl.NumberFormat("de-De", {
                  style: "currency",
                  currency: "EUR",
                }).format(checkoutPrice)}
              </h5>
            </div>
            <PayPalButton
              createOrder={(data, actions) => {
                return actions.order.create({
                  purchase_units: [
                    {
                      amount: {
                        currency_code: "EUR",
                        value: (
                          checkoutPrice +
                          (winesInCart.length > 0 ? shippingCost : 0)
                        ).toFixed(2),
                        breakdown: {
                          item_total: {
                            currency_code: "EUR",
                            value: checkoutPrice.toFixed(2),
                          },
                          shipping: {
                            currency_code: "EUR",
                            value: winesInCart.length > 0 ? shippingCost : 0,
                          },
                        },
                      },
                      items: [].concat(artInCart, winesInCart),
                    },
                  ],
                });
              }}
              onApprove={(data, actions) => {
                // Capture the funds from the transaction
                return actions.order.capture().then(async function (details) {
                  // Show a success message to your buyer
                  alert(
                    "Transaction completed by " +
                      details.payer.name.given_name +
                      ". The items will be delivered in 3-5 working days. Please contact us at +49-1766-9521145 for any further information. " +
                      "Your transaction ID is: " +
                      details.purchase_units[0].payments.captures[0].id +
                      "."
                  );

                  // OPTIONAL: Call your server to save the transaction
                  const baseURL = process.env.REACT_APP_BASE_URL;
                  console.log([].concat(artInCart, winesInCart));
                  await fetch(baseURL + "/purchases", {
                    method: "POST",
                    headers: {
                      "content-type": "application/json",
                    },
                    body: JSON.stringify({
                      orderID: data.orderID,
                      totalAmount: checkoutPrice,
                      products: [].concat(artInCart, winesInCart),
                    }),
                  });
                  // refresh the page
                  history.go();
                });
              }}
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default CheckoutPage;
