import React, { useState, useEffect } from "react";
import CheckoutNav from "./CheckoutNav";
import CheckoutPage from "./CheckoutPage";

const Checkout2 = ({ artCart, wineCart, checkoutPrice }) => {
  const [display, setDisplay] = useState(false);
  const [artInCart, setArtInCart] = useState([]);
  const [winesInCart, setWinesInCart] = useState([]);
  const shippingCost = 5.9;
  // Adding art PayPal object to cart
  useEffect(() => {
    setArtInCart(
      artCart.map((art) => {
        return {
          name: art.caption,
          artist: art.artist,
          sku: "art-" + art._id,
          description: art.artist,
          unit_amount: {
            value: art.price,
            currency_code: "EUR",
          },
          quantity: 1,
        };
      })
    );
  }, [artCart]);
  // Adding wine PayPal object to cart
  useEffect(() => {
    setWinesInCart(
      wineCart.map((wine) => {
        return {
          name: wine.title,
          description: "https://www.winesandcolors.de/",
          sku: "wine-" + wine._id,
          unit_amount: {
            value: wine.price,
            currency_code: "EUR",
          },
          quantity: wine.qty,
        };
      })
    );
  }, [wineCart]);

  return (
    <>
      {!display && (
        <CheckoutNav setDisplay={setDisplay} checkoutPrice={checkoutPrice} />
      )}
      {display && (
        <CheckoutPage
          setDisplay={setDisplay}
          artInCart={artInCart}
          winesInCart={winesInCart}
          checkoutPrice = {checkoutPrice}
          shippingCost={shippingCost}
        />
      )}
    </>
  );
};

export default Checkout2;
