import React from "react";

export default function CheckoutNav({ setDisplay, checkoutPrice }) {
  return (
    <nav className="navbar fixed-bottom bg-light">
      <button
        onClick={() => {
          setDisplay(true);
        }}
        type="button"
        className="btn btn-outline-primary btn-block"
      >
        Checkout{" "}
        {Intl.NumberFormat("de-De", {
          style: "currency",
          currency: "EUR",
        }).format(checkoutPrice)}
      </button>
    </nav>
  );
}
