import React, { useEffect, useState } from "react";
import Loader from "react-loader-spinner";
import { api_loadActiveArt, api_loadActiveWines } from "../../APIs";
import ArtGallery from "../../Components/ArtGallery";
import Checkout2 from "../../Components/Checkout2";
import WineCellar from "../../Components/WineCellar";

export default function WinesAndArt() {
  const [loading, setLoading] = useState(true);
  const [wines, setWines] = useState([]);
  const [art, setArt] = useState([]);
  const [artists, setArtists] = useState([]);
  const [artCart, setArtCart] = useState([]);
  const [wineCart, setWineCart] = useState([]);
  const [checkoutPrice, setCheckoutPrice] = useState(0);
  // Load art and wines
  useEffect(() => {
    const callAPI = async () => {
      setWines(await api_loadActiveWines().then((x) => x.json()));
      setArt(await api_loadActiveArt().then((x) => x.json()));
      setLoading(false);
    };
    callAPI();
  }, []);
  // Get an array of artists
  useEffect(() => {
    art.forEach((art) => {
      if (!artists.includes(art.artist)) setArtists([...artists, art.artist]);
    });
  }, [art, artists]);
  const addArtToCart = (product) => {
    product.isSelected = true;
    setArt(art.map((x) => {
      return x._id === product._id ? product : x;
    }))
    setArtCart([product]);
  };
  const removeArtFromCart = (product) => {
    product.isSelected = false;
    setArt(art.map((x) => {
      return x._id === product._id ? product : x;
    }))
    setArtCart([]);
  };
  const addWineToCart = (x) => {
    setWines(
      wines.map((wine) => {
        if (wine._id === x._id) {
          x.qty++;
          x.inventory--;
          return x;
        } else {
          return wine;
        }
      })
    );
    setWineCart(wines.filter((wine) => wine.qty > 0));
  };
  const removeWineFromCart = (x) => {
    setWines(
      wines.map((wine) => {
        if (wine._id === x._id) {
          x.qty--;
          x.inventory++;
          return x;
        } else {
          return wine;
        }
      })
    );
    setWineCart(wines.filter((wine) => wine.qty > 0));
  };
  // Update checkout price
  useEffect(() => {
    if (wines.length > 0 || art.length > 0) {
      setCheckoutPrice(
        wineCart.reduce((total, wine) => {
          return total + wine.qty * wine.price;
        }, 0) + (artCart[0] ? artCart[0].price : 0)
      );
    }
  }, [art.length, wines.length, artCart, wineCart]);
  return (
    <>
      {loading ? (
        // Loader
        <div
          className="d-flex justify-content-center align-items-center"
          style={{ height: "100vh" }}
        >
          <Loader type="Puff" color="#00BFFF" height={100} width={100} />
        </div>
      ) : (
        <>
          {wines.length > 0 && (
            <div id="wines">
              <WineCellar
                wines={wines}
                addWineToCart={addWineToCart}
                removeWineFromCart={removeWineFromCart}
              />
            </div>
          )}
          {art.length > 0 && (
            <div id="art">
              <ArtGallery
                art={art}
                artists={artists}
                addArtToCart={addArtToCart}
                removeArtFromCart={removeArtFromCart}
                artCart={artCart}
              />
            </div>
          )}
          {checkoutPrice > 0 && (
            <Checkout2
              artCart={artCart}
              wineCart={wineCart}
              checkoutPrice={checkoutPrice}
            />
          )}
        </>
      )}
    </>
  );
}
