import React, { useEffect } from "react";
import { api_loadAllArt } from "../APIs";
import NewOrUpdateArt from "./NewOrUpdateArt";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faArrowLeft } from "@fortawesome/free-solid-svg-icons";
import { Container } from "react-bootstrap";
import { useState } from "react";

export default function ArtAdminPanal(props) {
  const [galleryFromDB, setGalleryFromDB] = useState([])
  const [gallery, setGallery] = useState([]);
  const [searchKeyword, setSearchKeyword] = useState("");
  useEffect(() => {
    api_loadAllArt()
      .then((x) => x.json())
      .then((x) => {
        setGalleryFromDB(x);
        setGallery(x)
      });
  }, []);
  const addNewArt = (newArt) => {
    setSearchKeyword("");
    setGallery([newArt, ...gallery]);
    setGalleryFromDB([newArt, ...galleryFromDB]);
  };
  const updateArt = (art) => {
    const newGallery = gallery.map((x) => {
      return x._id === art._id ? art : x;
    });
    setGallery(newGallery);
    setGalleryFromDB(newGallery);
  };
  const searchArt = (event) => {
    event.preventDefault();
    setSearchKeyword(event.target.value);
    if (event.target.value.length > 0) {
      setGallery(galleryFromDB.filter(art=>{
        return art.artist.toLowerCase().includes(event.target.value) || art.caption.toLowerCase().includes(event.target.value)
      }));
    } else {
      setGallery(galleryFromDB);
    }
  };

  return (
    <Container fluid className="">
      <div className="sticky-top">
        <div className="py-2 d-flex justify-content-between">
          <form className="flex-grow-1">
            <input
              className="form-control rounded-pill py-0"
              autoFocus
              type="search"
              value={searchKeyword}
              onChange={searchArt}
              placeholder="Search Art"
            />
          </form>
          <NewOrUpdateArt addNewArt={addNewArt} />
          <button
            type="button"
            className="btn btn-outline-danger rounded-circle"
            onClick={() => props.goBack()}
          >
            <FontAwesomeIcon icon={faArrowLeft} />
          </button>
        
        </div></div>
      <div className="table-responsive">
        <table className="table table-borderless table-sm ">
          <thead>
            <tr>
              <th scope="col">#</th>
              <th scope="col">Artist</th>
              <th scope="col">Title</th>
              <th scope="col" className="text-right d-none d-md-block">H x W</th>
              <th scope="col" className="text-right">Price (€)</th>
              <th scope="col">Sold?</th>
              <th scope="col">Update</th>
            </tr>
          </thead>
          <tbody>
            {gallery.map((art, index) => (
              <tr key={art._id}>
                <th scope="row">{index + 1}</th>
                <td>{art.artist}</td>
                <td>{art.caption}</td>
                <td className="text-right d-none d-md-block">
                  {art.thumbnailHeight} x {art.thumbnailWidth}
                </td>
                <td className="text-right">
                  {Intl.NumberFormat("de-De", {
                    style: "currency",
                    currency: "EUR",
                  }).format(art.price)}
                </td>
                <td>{art.active ? "No" : "Yes"}</td>
                <td>
                  <NewOrUpdateArt art={art} updateArt={updateArt} />
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </Container>
  );
}
