import React from "react";
import ArtByArtist from "./ArtByArtist";
import BannarText from "./BannarText";

const ArtGallery = ({
  art,
  artists,
  addArtToCart,
  removeArtFromCart,
  artCart,
}) => {
  return (
    <div>
      {art && (
        <div className="">
          <BannarText text="... & color your mind!" />
          {artists.map((artist, index) => (
            <ArtByArtist
            key={index}
              artist={artist}
              art={art.filter((x) => x.artist === artist)}
              artCart={artCart}
              addArtToCart={addArtToCart}
              removeArtFromCart={removeArtFromCart}
            />
          ))}
        </div>
      )}
    </div>
  );
};

export default ArtGallery;
